<template>
  <div id="home">
   <!-- 
     title是从$emit()第二个参数传过来的值 
   -->
   <Header v-on:titleChanged="updateTitle($event)" 
   :title="title" />
    <!-- 父传 -->
   <Users :users="users" />
   <Users :users="users" />
   <Footer :title="title" />
  </div>
</template>

<script>
import Users from './Users.vue'
import Header from './Header.vue'
import Footer from './Footer.vue'


export default {
  name: 'home',
  data(){
    return {
      users:[
       /* 
        {name:"Jack",age:'18',show:false},
        {name:"lucy",age:'16',show:false},
        {name:"Jack",age:'18',show:false},
        {name:"Jack",age:'18',show:false},  
       */

      ],
      title:"传递的是一个值,(number string boolean)"
    }
  },
  methods:{
    // 改变当前的内容
    updateTitle(title){
      this.title = title;
    }
  },
  components: {
    Users,
    Header,
    Footer 
  },
  created(){
      this.$http.get("http://jsonplaceholder.typicode.com/users")
      .then((date)=>{
         this.users = date.body;
      })
  }

}
</script>

