<template>
  <!-- 触发changeTitle方法 -->
    <header @click="changeTitle">
        <h1>{{title1}} {{title}}</h1>
    </header>
</template>

<script>
export default {
  name:'app-header',
  props:{
    title:{
      type:String
    }
  },
  data(){
    return{
       title1: "Vue2.demo"
    }
  },
  methods:{
    changeTitle:function(){
      // this.title = "changed"
      /* 注册事件titleChanged 传递了一个参数子向父组件传值
       去父级找titleChanged事件执行一个方法updateTitle()*/
      this.$emit("titleChanged","子向父组件传值");
    }
  },
 /* beforeCreate:function(){
    alert('组件实例化之前执行的函数');
  },
   created:function(){
    alert('组件实例化完毕,页面未显示');
  },
  beforeMount:function(){
    alert('组件挂载前,页面仍未显示,虚拟DOM已配置');
  },
  mounted:function(){
    alert('组件挂载后,页面显示');
  },
  beforeUpdate:function(){
    alert('组件更新前,页面仍未更新,虚拟DOM已配置');
  },
 updated:function(){
    alert('组件更新后,页面更新完成,页面显示');
  },
  beforeDestroy:function(){
    alert('组件销毁前');
  },
  destroyed:function(){
    alert('组件销毁后');
  }
  */
}
</script>

